.tech-stack {
  width: 100vw;
  background-color: #202020;
  overflow-x: hidden;
  overflow-y: hidden;
  padding-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.tech-stack .front-end, .tech-stack .other, .tech-stack .back-end {
  width: 100%;
  max-width: 800px;
  position: relative;
  display: grid;
  place-items: center;
  grid-template-columns: 1fr 1fr;
}
.tech-stack .front-end > div, .tech-stack .other > div, .tech-stack .back-end > div {
  margin: 1rem;
  min-width: 220px;
  max-width: 400px;
  font-size: large;
}
.tech-stack .front-end {
  margin-top: 2rem;
}
.tech-stack .back-end {
  direction: rtl;
}
.tech-stack h2 {
  color: #00a2ff;
}
.tech-stack p {
  color: rgb(107, 107, 107);
  font-style: italic;
  margin-top: 0.5rem;
}
.tech-stack p > b {
  color: rgb(150, 150, 150);
}

